import React from 'react'
import { Link } from 'react-router-dom'
import ScreenWrapper from '../components/ScreenWrapper'
import Colors from '../constants/Colors'
import ueaLogo from '../images/uaelg.png'
// Languages:
import { useTranslation } from 'react-i18next'

const Signup1 = () => {
    const { t, i18n } = useTranslation()
    const [selected, setSelected] = React.useState(false)
    const selectItem = () => {
        document.getElementById('check1').setAttribute("checked", true)
        setTimeout(() => {
            setSelected(true)
        }, 10);
    }
    const closeItem = () => {
        setSelected(false)
        setTimeout(() => {
            document.getElementById('check1').setAttribute("checked", true)
        }, 100);

    }
    return (
        <ScreenWrapper dir={t('En') === '1' ? '' : 'rtl'}>
            <h1 className="h1-heading">{t('CreateAnd')}</h1>
            {!selected &&
                <form action="" className="form">
                    <input type="text" placeholder={t('YourName')} className="input-el" />
                    <input type="text" placeholder={t('BusinessName')} className="input-el" />
                    <div className="phone-input">
                        <div className="country">
                            <img src={ueaLogo} alt="uaelogo" />
                            <span>+965</span>
                        </div>
                        <input type="text" placeholder="00000000" className="input-el" />
                    </div>
                    <input type="email" placeholder={t('Email')} className="input-el" />
                    <input type="password" placeholder={t('Password')} className="input-el" />
                    <div className="form-check">
                        <input type="checkbox" id="check1" onClick={() => selectItem()} />
                        <label style={{ color: Colors.main }} htmlFor="check1">{t('AcceptTerms')}</label>
                    </div>
                </form>}
            {selected &&
                <div className="feature__el">
                    <h3>{t('Privacy')}</h3>
                    <button onClick={() => closeItem()}>X</button>
                </div>
            }
            <Link className="our-btn" to="/signup2">{t('Next')}</Link>
        </ScreenWrapper>
    )
}

export default Signup1
