import React from 'react'
import { Link } from 'react-router-dom'
import ScreenWrapper from '../components/ScreenWrapper'
// Languages:
import { useTranslation } from 'react-i18next'

const Contact = () => {
    const { t, i18n } = useTranslation()
    return (
        <ScreenWrapper dir={t('En') === '1' ? '' : 'rtl'}>
            <div className="contact">
                <h1 className="h1-heading">{t('TalkTo')}</h1>
                <form action="" className="about-form">
                    <div className="contact__form-item">
                        <input type="text" placeholder={t('Name')} className="input-el" />
                        <input type="text" placeholder={t('Mobile')} className="input-el" />
                    </div>
                    <input type="email" placeholder={t('Email')} className="input-el" />
                    <textarea cols="20" rows="6" className="input-el" placeholder={t('TakeOur')}></textarea>
                </form>
                <Link className="our-btn our-btn-outline" to="/signup1">{t('Send')}</Link>
            </div>
        </ScreenWrapper>
    )
}

export default Contact
