import React from 'react'
import Navbar from '../components/Navbar'
import { Link } from 'react-router-dom'
import ScreenWrapper from '../components/ScreenWrapper'
// Languages:
import { useTranslation } from 'react-i18next'

const Home = () => {
    const { t, i18n } = useTranslation()
    const changelanguageToAr = () => i18n.changeLanguage('ar')
    const changelanguageToEn = () => i18n.changeLanguage('en')
    return (
        <ScreenWrapper dir={t('En') === '1' ? '' : 'rtl'}>
            <h1 className="h1-heading">{t('ShiftYour')}</h1>
            <p className="p-text">{t('WithShift')} </p>
            <p className="p-text">{t('Cool')}</p>
            <Link className="our-btn" to="/signup1">{t('HitIt')}</Link>
        </ScreenWrapper>
    )
}

export default Home
